import { h, render, DefineComponent } from 'vue'

/**
 * node
 * @param dom dom
 * @param options 
 * @returns
 */
export function createGlobalNode(dom: DefineComponent<object, object, any>, options: object) {
  // componentsDOM
  const vnode = h(dom, options)
  // div
  const div = document.createElement('div')
  // container append body
  document.body.append(div)
  //  div
  render(vnode, div)
  return { vnode, div }
}

/**
 * 
 * @param el 
 */
export function removeGlobalNode(el: HTMLElement) {
  try {
    el && el.remove()
  } catch (e) {
    console.log(e)
  }
}
